<template>

  <v-container class="align-content-center" style="height: 100%; position: relative; display: grid;">

    <div>
      <v-card
          id="card"
          class="my-16 align-center"
          elevation="2"
          max-height="600"
          max-width="600"
      >

        <v-card-title class="text-h4 font-weight-bold text-center">Resume {{ dryerId }}</v-card-title>

        <v-row align="center" class="ma-4">
          <v-col class="d-flex justify-center">
            <v-btn height="40" width="100" @click="confirmBtn('30')">30 min</v-btn>
          </v-col>
          <v-col class="d-flex justify-center">
            <v-btn height="40" width="100" @click="confirmBtn('60')">60 min</v-btn>
          </v-col>
        </v-row>

        <v-row align="center" class="ma-4">
          <v-col class="d-flex justify-center">
            <v-btn height="40" width="100" @click="confirmBtn('90')">90 min</v-btn>
          </v-col>
          <v-col class="d-flex justify-center">
            <v-btn height="40" width="100" @click="confirmBtn('120')">120 min</v-btn>
          </v-col>
        </v-row>

        <v-row align="center" class="ma-4">
          <v-col class="d-flex justify-center">
            <v-btn height="40" width="100" @click="confirmBtn('150')">150 min</v-btn>
          </v-col>
          <v-col class="d-flex justify-center">
            <v-btn height="40" width="100" @click="confirmBtn('180')">180 min</v-btn>
          </v-col>
        </v-row>

      </v-card>
    </div>

  </v-container>
</template>

<script>
import VueJwtDecode from "vue-jwt-decode";
import dayjs from "dayjs";
import { VueSimpleAlert } from "vue3-simple-alert";

export default {
  name: "resumeButton",
  data() {
    return {
      dryerId: '',
      token: window.location.href.split("/")[3].split("=")[1]
    }
  },
  async mounted() {
    await this.init()
  },
  computed: {
    // isComplete() {
    //   return (
    //       this.dataInput.U1 != '' &&
    //       this.dataInput.U2 != '' &&
    //       this.dataInput.U3 != '' &&
    //       this.dataInput.D1 != '' &&
    //       this.dataInput.D2 != '' &&
    //       this.dataInput.D3 != '' &&
    //       !this.isExpire
    //   );
    // }
  },
  methods: {
    async init() {
      try {
        let result = VueJwtDecode.decode(this.token);
        this.dryerId = result.dryerId;
        this.expireDate = result.exp;
        this.expireDate = dayjs.unix(result.exp).toISOString();
        let now = dayjs().toISOString();
        if (now < this.expireDate) {
          this.isCountdown = true;
          this.isExpire = false;
        } else {
          this.isExpire = true;
        }
      } catch (e) {
        console.log(e);
      }
    },
    confirmBtn(duration) {
      VueSimpleAlert.confirm("Are you sure you want to resume this " + this.dryerId + " for " + duration + " minutes?", "Resume Batch", "question")
          .then(() => {
            VueSimpleAlert.alert("You have resume this " + this.dryerId + " for " + duration + " minutes.")
          })
          .catch(() => {
            console.log("Canceled");
          });
    }
  },
  // components: { VueSimpleAlert },
}
</script>

<style>
#card {
  margin: auto;
  padding: 25px;
}
</style>
